.button {
  margin-top: auto;
  align-self: center;
  color: #fff;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  text-align: center;
  width: 100%;
  height: 54px;
  border: none;
  box-sizing: border-box;
  background-color: var(--color-accent);
  padding: 18px 24px;
  border-radius: 4px;
  cursor: pointer;
  transition: all .3s;
}

.button:hover {
  box-shadow: 0px 4px 4px 0px rgba(0,0,0,0.3);
  transition: all .3s;
}

.button:disabled {
  background-color: var(--color-support);
  cursor: default;
}

.button:hover:disabled {
  box-shadow: none;
}